import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { AuthService } from '../../../service/authorization/auth.service';
import { UserService } from '../../../../app/service/user.service';
import { DefaulHomepage } from 'src/app/shared/constants/constant';
import { StoreForgotPasswordService } from 'src/app/service/store-forgot-password.service';

@Component({
  selector: 'app-forgot',
  templateUrl: './forgot.component.html'
})
export class ForgotComponent implements OnInit {

  @Input()
  public form_data: FormGroup;
  public lableMessage: any;
  public checkMesage = false;
  public defaulHomepage = DefaulHomepage;
  constructor(private userApiService: UserService, private fb: FormBuilder, private route: Router,
    private auth: AuthService, private storeForgotPasswordService: StoreForgotPasswordService) { }

  ngOnInit() {
    if (this.auth.isLoggednIn()) {
      this.defaulHomepage();
    }
    this.onInitForm();
  }

  onForgotPassword() {
    if (this.form_data.valid) {
      const form = this.form_data.value;
      this.userApiService.getIdByAccountName(form).subscribe((event: any) => {
        this.checkMesage = false;
        if (event) {
          this.onSendSMS(event);
        } else {
          swal.fire('Error', 'Thất bại.', 'error');
        }
      }, (err: HttpErrorResponse) => {
        this.checkMesage = false;
        if (err && err.error) {
          swal.fire('Error', err.error.message, 'error');
        } else {
          swal.fire('Error', 'Lỗi kết nối hệ thống server.', 'error');
        }
      });
    } else {
      this.checkMesage = true;
      this.lableMessage = 'Tài khoản không được để trống.';
    }
  }
  onSendSMS(data) {
    this.userApiService.sendSMS({ u_id: data._id }).subscribe((result: any) => {
      if (result) {
        this.storeForgotPasswordService.updateStore(data);
        return this.route.navigate(['auth', 'get-password']);
      } else {
        swal.fire('Error', 'Quá trình gửi mã OTP bị lỗi.', 'error');
      }
    }, (err: HttpErrorResponse) => {
      if (err.error && err.error.message) {
        swal.fire('Error', err.error.message, 'error');
      } else {
        swal.fire('Error', 'Lỗi kết nối hệ thống server.', 'error');
      }
    });
  }
  private onInitForm() {
    this.form_data = this.fb.group({
      account_name: new FormControl('', Validators.compose([Validators.required]))
    });
  }

  onCancel() {
    this.route.navigate(['auth', 'login']);
  }
}
