export function getErrorMessage(formControl, errorMessagesConfig = []) {
  let errorField = "";
  Object.keys(formControl.controls).every(item => {
    if (!formControl.controls[item].valid) {
      const errors = Object.keys(formControl.controls[item].errors);
      errorField = `${item}.${errors[0]}`;
      return false;
    } else {
      return true;
    }
  });
  const errorConfig = errorMessagesConfig.find(d => d.key === errorField);
  if (!errorConfig) {
    console.log(errorField);
  }
  return errorConfig ? errorConfig.message : "Lỗi validate";
}
