import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators, AbstractControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '../../../service/authorization/auth.service';
import { UserService } from '../../../service/user.service';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html'
})
export class ResetPasswordComponent implements OnInit {

  @Input()
  public form_data: FormGroup;
  public lableError: any;
  public checkMesage = true;
  public setIcon = false;
  private token = '';


  constructor(private userApiService: UserService, private fb: FormBuilder,
    private activatedRoute: ActivatedRoute, private route: Router,
    private auth: AuthService) {
    this.onInitForm();
  }

  ngOnInit() {
    if (!this.auth.isLoggednIn()) {
      return this.route.navigate(['auth', 'login']);
    }
    this.token = this.activatedRoute.snapshot.paramMap.get('token');

  }

  onResetPassword() {
    if (this.form_data.valid) {
      const form = this.form_data.value;
      this.userApiService.resetPassword(form).subscribe((event: any) => {
        if (event.data.length > 0) {
          this.token = '';
          this.checkMesage = true;
          Swal.fire('Success', 'Đặt lại mật khẩu thành công.', 'success');
        } else {
          this.checkMesage = false;
          this.lableError = event.message;
        }
      }, (err: HttpErrorResponse) => {
        Swal.fire('Error', 'Lổi kết nối hệ thống serve.', 'error');
      });
    } else {
      this.checkMesage = false;
      if (!this.form_data.controls.new_password.value) {
        return this.lableError = 'Mật khẩu mới không được để trống.';
      }
      if (!this.form_data.controls.rePassword.value) {
        return this.lableError = 'Xác nhận mật khẩu không được để trống.';
      } else {
        return this.lableError = 'Xác nhận mật khẩu không đúng.';
      }
    }

  }

  private onInitForm() {
    this.form_data = this.fb.group({
      new_password: new FormControl('', Validators.compose([Validators.required])),
      rePassword: new FormControl('', Validators.compose([Validators.required])),
      token: new FormControl(this.token, Validators.required)
    }, {
      // your validation method
      validator: function MatchPassword(AC: AbstractControl) {
        const password = AC.get('new_password').value;
        const rePassword = AC.get('rePassword').value;
        if (password !== rePassword) {
          AC.get('rePassword').setErrors({ MatchPassword: true });
        } else {
          return null;
        }
      }
    });
  }

  onCancel() {
    this.route.navigate(['auth', 'login']);
  }

}
