import { Component, OnInit } from "@angular/core";
import { getErrorMessage } from "../../../shared/utilities/validatorHelper";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
  AbstractControl,
} from "@angular/forms";
import { HttpResponse, HttpErrorResponse } from "@angular/common/http";

import Swal from "sweetalert2";

@Component({
  selector: "app-signup",
  templateUrl: "./signup.component.html",
})
export class SignupComponent {
  public getErrorMessage = getErrorMessage;
  public errorMessage = null;
  public formData: FormGroup;
  constructor(private fb: FormBuilder) {}
  ngOnInit() {
    this.formData = this.fb.group({
      full_name: new FormControl("", Validators.required),
      phone_number: new FormControl(
        "",
        Validators.compose([
          Validators.required,
          Validators.minLength(10),
          Validators.maxLength(11),
        ])
      ),
      email: new FormControl("", [Validators.required, Validators.email]),
      id_card: new FormControl("", [Validators.required, CustomValidateIDCard]),
    });
  }
  onSubmit() {
    this.errorMessage = null;
    if (this.formData.valid) {
    } else {
      const errorMessagesConfig = [
        {
          key: "full_name.required",
          message: "Họ và tên không được để trống",
        },
        {
          key: "phone_number.required",
          message: "Số điện thoại không được để trống",
        },
        {
          key: "phone_number.maxlength",
          message: "Số điện thoại không hợp lệ",
        },
        {
          key: "phone_number.minlength",
          message: "Số điện thoại không hợp lệ",
        },
        {
          key: "email.required",
          message: "Email không được để trống",
        },
        {
          key: "email.email",
          message: "Email không đúng định dạng (example._1@gmail.com)",
        },
        {
          key: "id_card.required",
          message: "CMT/CCCD không được để trống",
        },
        {
          key: "id_card.invalid",
          message: "CMT/CCCD không hợp lệ",
        },
      ];
      this.errorMessage = getErrorMessage(this.formData, errorMessagesConfig);
    }
  }
}

