import { Component, OnInit, Input } from "@angular/core";
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
  AbstractControl,
} from "@angular/forms";
import swal from "sweetalert2";
import { HttpErrorResponse } from "@angular/common/http";
import { Router, ActivatedRoute } from "@angular/router";
import { AuthService } from "../../../../service/authorization/auth.service";
import Swal from "sweetalert2";
import { UserService } from "src/app/service/user.service";
import { DefaulHomepage } from "src/app/shared/constants/constant";
import { StoreForgotPasswordService } from "src/app/service/store-forgot-password.service";

@Component({
  selector: "app-get-password-forgot",
  templateUrl: "./get-password-forgot.component.html",
})
export class GetPasswordForgotComponent implements OnInit {
  public form_data: FormGroup;
  public lableMessage: any;
  public checkMesage = false;
  public data: any;
  public defaulHomepage = DefaulHomepage;
  constructor(
    private userApiService: UserService,
    private fb: FormBuilder,
    private route: Router,
    private auth: AuthService,
    private activatedRoute: ActivatedRoute,
    private storeForgotPasswordService: StoreForgotPasswordService
  ) {
    this.onInitForm();
  }

  ngOnInit() {
    if (this.auth.isLoggednIn()) {
      this.defaulHomepage();
    }
    // this.id = this.activatedRoute.snapshot.paramMap.get('id');
    this.storeForgotPasswordService.currentStore.subscribe((msg) => {
      if (msg) {
        this.data = msg;
      }
    });
  }

  onForgotPassword() {
    if (this.form_data.valid && this.data) {
      const form = this.form_data.value;
      const obj = {
        _id: this.data._id,
        newPassword: form.newPassword,
        otp: form.otp,
      };
      const isAccount = this.data.scope_access !== "SALE";
      this.userApiService.forgotPassword(obj, isAccount).subscribe(
        (event: any) => {
          if (event) {
            this.checkMesage = false;
            // Swal.fire({
            //   title: 'Lấy mật khẩu thành công',
            //   text: 'Nhấn OK để đăng nhập lại vào ứng dụng.',
            //   type: 'success',
            //   confirmButtonColor: '#3085d6',
            //   cancelButtonColor: '#d33',
            //   confirmButtonClass: 'btn btn-success m-r-10',
            //   confirmButtonText: `OK`,
            //   allowOutsideClick: false
            // }).then((result) => {
            //   if (result.value) {
            //     this.auth.logout();
            //   }
            // });
          } else {
            this.checkMesage = false;
            swal.fire("Error", "Hành động thất bại.", "error");
          }
        },
        (err: HttpErrorResponse) => {
          this.checkMesage = false;
          if (err && err.error) {
            swal.fire("Error", err.error.message, "error");
          } else {
            swal.fire("Error", "Lổi kết nối hệ thống serve.", "error");
          }
        }
      );
    } else {
      this.checkMesage = true;
      this.lableMessage = "";
      if (!this.form_data.controls.otp.value) {
        return (this.lableMessage = "Mã OTP không được để trống.");
      }
      if (!this.form_data.controls.newPassword.value) {
        return (this.lableMessage = "Mật khẩu mới không được để trống.");
      }
      if (!this.form_data.controls.confirmNewPassword.value) {
        return (this.lableMessage = "Xác nhận mật khẩu không được để trống.");
      } else if (!this.form_data.controls.confirmNewPassword.valid) {
        return (this.lableMessage = "Xác nhận mật khẩu không đúng.");
      }
    }
  }

  private onInitForm() {
    this.form_data = this.fb.group(
      {
        newPassword: new FormControl(
          "",
          Validators.compose([Validators.required])
        ),
        confirmNewPassword: new FormControl(
          "",
          Validators.compose([Validators.required])
        ),
        otp: new FormControl("", Validators.compose([Validators.required])),
      },
      {
        // validation method
        validator: function MatchPassword(AC: AbstractControl) {
          const password = AC.get("newPassword").value;
          const rePassword = AC.get("confirmNewPassword").value;
          if (password !== rePassword) {
            AC.get("confirmNewPassword").setErrors({ MatchPassword: true });
          } else {
            return null;
          }
        },
      }
    );
  }

  onCancel() {
    this.route.navigate(["auth", "login"]);
  }
}
