import { Routes } from "@angular/router";

import { NotfoundComponent } from "./404/not-found.component";
import { LockComponent } from "./lock/lock.component";
import { LoginComponent } from "./login/login.component";
import { SignupComponent } from "./signup/signup.component";
import { ResetPasswordComponent } from "./reset-password/reset-password.component";
import { ForgotComponent } from "./forgot/forgot.component";
import { GetPasswordForgotComponent } from "./forgot/get-password-forgot/get-password-forgot.component";
import { VerifyODPComponent } from "./verify-odp/verify-odp.component";

export const AuthenticationRoutes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        redirectTo: "login",
        pathMatch: "full",
      },
      {
        path: "verify-odp",
        component: VerifyODPComponent,
      },
      {
        path: "404",
        component: NotfoundComponent,
      },
      {
        path: "lock",
        component: LockComponent,
      },
      {
        path: "forgot-password",
        component: ForgotComponent,
      },
      {
        path: "reset-password/:token",
        component: ResetPasswordComponent,
      },
      {
        path: "login",
        component: LoginComponent,
      },
      {
        path: "signup",
        component: SignupComponent,
      },
      {
        path: "get-password",
        component: GetPasswordForgotComponent,
      },
    ],
  },
];
